import React from 'react';

import { closeWindowIcon } from './assets';
import { ButtonWidget } from './button_widget';

export const PopupWidget = (
  {
    title,
    close,
    createWindowContentWidget,
  }: {
    title: string;
    close: () => void;
    createWindowContentWidget: () => React.ReactNode;
  },
) => {

  return <div
    style={{
      width: `100%`,
      height: `100%`,
      pointerEvents: `initial`,

      ...{
        WebkitAppRegion: `no-drag`,
      },

      display: `flex`,
      justifyContent: `center`,
      alignItems: `center`,

      backgroundColor: `#00000090`,
    }}
  >
    <div
      style={{
        position: `absolute`,
        width: `100%`,
        height: `100%`,
      }}
      onClick={close}
    />
    <div
      style={{
        width: `170px`,
        height: `155px`,

        background: `linear-gradient(180deg, rgba(255, 255, 255, 0) 70.73%, rgba(255, 255, 255, 0.05) 100%), rgba(30, 35, 40, 0.8)`,
        backdropFilter: `blur(12px)`,
      }}
    >
      <div
        style={{
          width: `100%`,
          height: `100%`,

          display: `flex`,
          flexDirection: `column`,
          justifyContent: `flex-start`,
        }}
      >
        <div
          style={{
            width: `100%`,
            height: `28px`,
            flexGrow: 0,
            flexShrink: 0,

            backgroundColor: `transparent`,
          }}
        >
          <div
            style={{
              width: `100%`,
              height: `100%`,

              display: `flex`,
              justifyContent: `space-between`,
              alignItems: `center`,
              paddingTop: `5px`,
            }}
          >
            <div
              style={{

                fontFamily: `Roboto`,
                fontSize: `13px`,
                color: `#ffffff`,
                position: `absolute`,
                width: `100%`,
                textAlign: `center`,
                lineHeight: `25px`,
              }}
              children={title}
            />
            <ButtonWidget
              htmlStyle={{
                cursor: `pointer`,
                position: `absolute`,
                right: `10px`,
              }}
              onClicked={close}
              createChildWidget={({ isHovered }) => <img
                style={{
                  width: `13px`,
                  height: `13px`,
                  padding: `5px`,

                  background: isHovered
                    ? `linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), linear-gradient(165.43deg, rgba(255, 255, 255, 0.12) -3.58%, rgba(255, 0, 0, 0.6) 89.69%)`
                    : `transparent`,
                }}
                src={closeWindowIcon}
              />}
            />
          </div>
        </div>
        <div
          style={{
            width: `100%`,
            flexBasis: 0,
            flexGrow: 1,
            flexShrink: 0,
          }}
        >
          {createWindowContentWidget()}
        </div>
      </div>
    </div>
  </div>;
};
