import React from 'react';
import { backgroundImg, errorIcon, logoIcon } from './assets';

import { LabelButtonWidget } from './label_button_widget';

export const ErrorWidget = (
  {
    description,
    refresh,
  }: {
    description: string;
    refresh: () => void;
  },
) => {
  return (
  <div
    style={{
      width: `100%`,
      height: `100%`,

      display: `flex`,
      flexDirection: `column`,
      justifyContent: `flex-start`,
      alignItems: `stretch`,
    }}
  >
    <div
      style={{
        width: `100%`,
        height: `calc(100% - 32px)`,
        flexGrow: 0,
        flexShrink: 0,
      }}
    >
      <img
        style={{
          width: `100%`,
          height: `100%`,

          objectFit: `cover`,
        }}
        src={backgroundImg}
      />
      <img
        style={{
          position: `absolute`,
          bottom: `30px`,
          left: `25%`,
          width: `50%`,
          height: `auto`,
        }}
        src={logoIcon}
      />
    </div>
    <div
      style={{
        width: `100%`,
        flexBasis: 0,
        flexGrow: 1,
        flexShrink: 0,
        background: `rgba(255, 255, 255, 0.05)`,
      }}
    >
      <div
        style={{
          width: `100%`,
          height: `100%`,
          display: `flex`,
          flexDirection: `column`,
          justifyContent: `center`,
          alignItems: `stretch`,
        }}
      >
        <div
          style={{
            position: `absolute`,
            paddingLeft: `10px`,
            paddingTop: `2px`,
          }}
        >{[
          <div 
            style={{
              width: `18px`,
              marginRight: `5px`,
            }}
          >
            <img src={errorIcon} style={{ width: `100%` }} />
          </div>,
          <div
            style={{
              fontFamily: `Roboto`,
              fontWeight: 500,
              fontSize: `13px`,
              color: `#D50101`,
              lineHeight: `25px`,
            }}
            onClick={refresh}
          >
            {description}
          </div>,
        ]}</div>
        <div
          style={{
            position: `absolute`,
            right: `10px`,
          }}
        >{[
          <LabelButtonWidget
            htmlStyle={{
              cursor: `pointer`,
              background: `linear-gradient(147.9deg, rgba(255, 0, 0, 0.6) 9.15%, rgba(255, 0, 0, 0.6) 80.72%)`,
              color: `white`,
              boxSizing: `border-box`,
              display: `flex`,
              flexDirection: `row`,
              justifyContent: `center`,
              alignItems: `center`,
              padding: `6px 12px`,
              gap: `12px`,
              position: `relative`,
              width: `100px`,
              height: `22px`
            }}
            labelWidget={{
              getHtmlStyle: ({ isHovered }) => ({
                fontFamily: `Roboto`,
                fontSize: `13px`,
                color: isHovered
                  ? `linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), linear-gradient(165.43deg, rgba(255, 255, 255, 0.12) -3.58%, rgba(255, 0, 0, 0.6) 89.69%)`
                  : `#FFFFFF`,
              }),
              text: `RETRY`,
            }}
            onClicked={refresh}
          />,
        ]}</div>
      </div>
    </div>
  </div>
  );
};
