import React from 'react';
import { minimizeWindowAsync, startQuitTaskAsync } from 'patchkit-generic-patcher2_ipc';

import { assert } from './assert';
import { useState } from './use_state';
import { markIcon, minimizelIcon, closeIcon } from './assets';
import { openOptionsPopup, openAppChangelogPopup } from './theme_state';
import { ButtonWidget } from './button_widget';
import { LabelButtonWidget } from './label_button_widget';

export const WindowTitleBarWidget = (
  {
    appId,
  }: {
    appId: string | undefined;
  },
) => {

  const state = useState();

  const appState = (() => {

    if (state === undefined || appId === undefined) {

      return undefined;
    }

    const result = state.apps[appId];

    assert(result !== undefined);

    return result;
  })();

  return <div
    style={{
      width: `100%`,
      height: `100%`,

      display: `flex`,
      justifyContent: `space-between`,

      ...{
        WebkitAppRegion: `drag`,
      },
    }}
  >
    <div
      style={{
        visibility: `hidden`,
        position: `absolute`,
        width: `100%`,
        height: `100%`,

        display: `flex`,
        justifyContent: `center`,
        alignItems: `center`,

        fontFamily: `Roboto`,
        fontWeight: 600,
        fontSize: `14px`,
        color: `#ffffff`,
      }}
    >
      {appState?.name}
    </div>
    <div
      style={{
        height: `100%`,
        marginRight: `24px`,

        display: `flex`,
        justifyContent: `flex-start`,
        alignItems: `center`,
      }}
    >
      <div
        style={{
          width: `32px`,
          height: `32px`,
          marginLeft: `10px`,
          marginRight: `10px`,
          display: `flex`,
          justifyContent: `center`,
          alignItems: `center`,
        }}
      >
        <img
          style={{
            width: `32px`,
            height: `auto`,
          }}
          src={markIcon}
        />
      </div>
      {state === undefined || appId === undefined
        ? []
        : [
          <LabelButtonWidget
            key={0}
            htmlStyle={{
              cursor: `pointer`,
              ...{
                WebkitAppRegion: `no-drag`,
              },
            }}
            labelWidget={{
              getHtmlStyle: ({ isHovered }) => ({

                fontFamily: `Roboto`,
                fontStyle: `normal`,
                fontSize: `13px`,
                lineHeight: `23px`,
                textTransform: `uppercase`,
                color: isHovered
                  ? `#dddddd`
                  : `#ffffff`,
              }),
              text: `OPTIONS`,
            }}
            onClicked={() => openOptionsPopup({ appId })}
          />,
          <div
            key={1}
            style={{
              width: `12px`,
            }}
          />,
          <LabelButtonWidget
            key={2}
            htmlStyle={{
              cursor: `pointer`,
              ...{
                WebkitAppRegion: `no-drag`,
              },
            }}
            labelWidget={{
              getHtmlStyle: ({ isHovered }) => ({

                display: `none`,
                fontFamily: `Roboto`,
                // fontWeight: 500,
                fontSize: `12px`,
                color: isHovered
                  ? `#dddddd`
                  : `#ffffff`,
              }),
              text: `CHANGELOG`,
            }}
            onClicked={() => openAppChangelogPopup({ appId })}
          />,
        ]
      }
    </div>
    <div
      style={{
        height: `100%`,

        display: `flex`,
        justifyContent: `flex-end`,
      }}
    >
      <ButtonWidget
        htmlStyle={{
          cursor: `pointer`,
          ...{
            WebkitAppRegion: `no-drag`,
          },
        }}
        onClicked={async () => await minimizeWindowAsync({})}
        createChildWidget={({ isHovered }) => <img
          style={{
            width: `16px`,
            height: `auto`,
            paddingTop: `11px`,
            paddingBottom: `11px`,
            paddingLeft: `4px`,
            paddingRight: `4px`,
            marginTop: `5px`,
            marginBottom: `5px`,
            marginRight: `20px`,

            background: isHovered
              ? `linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), linear-gradient(165.43deg, rgba(255, 255, 255, 0.12) -3.58%, rgba(255, 0, 0, 0.6) 89.69%)`
              : `transparent`,
          }}
          src={minimizelIcon}
        />}
      />
      <ButtonWidget
        htmlStyle={{
          cursor: `pointer`,
          ...{
            WebkitAppRegion: `no-drag`,
          },
        }}
        onClicked={async () => await startQuitTaskAsync({})}
        createChildWidget={({ isHovered }) => <img
          style={{
            width: `14px`,
            height: `14px`,
            paddingTop: `5px`,
            paddingBottom: `5px`,
            paddingLeft: `5px`,
            paddingRight: `5px`,
            marginTop: `5px`,
            marginBottom: `5px`,
            marginRight: `5px`,

            background: isHovered
              ? `linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), linear-gradient(165.43deg, rgba(255, 255, 255, 0.12) -3.58%, rgba(255, 0, 0, 0.6) 89.69%)`
              : `transparent`,
          }}
          src={closeIcon}
        />}
      />
    </div>
  </div>;
};
