import React from 'react';

import { AppBranchTaskProgressBarWidget } from './app_branch_task_progress_bar_widget';
import { TaskState } from './task_state';

export function AppBranchTaskBaseWidget({
  taskState,
  action,
}: {
  taskState?: string;
  action?: () => void;
}): JSX.Element {

  const bottomBarWidgetText = (() => {
    switch (taskState) {
      case TaskState.Uninstall:
        return `UNINSTALLING GAME...`;
      default:
        return ``;    
    }
    
  })();

  return <div
    style={{
      width: `100%`,
      height: `100%`,
      display: `flex`,
      flexDirection: `column`,
      justifyContent: `center`,
      alignItems: `stretch`,
    }}
  >
    <div
      style={{
        width: `100%`,
        height: `32px`,
      }}
    >
      <AppBranchTaskProgressBarWidget
        fillWidget={{
          shouldBeAnimatedIfPercentageIsUndefined: true,
          htmlStyle: {
            background: `linear-gradient(95.2deg, rgba(255, 0, 0, 0.36) 4.17%, rgba(255, 0, 0, 0.6) 95.83%)`,
          },
        }}
        percentage={undefined} />
    </div>
    <div
      style={{
        position: `absolute`,
        paddingLeft: `10px`,
        paddingTop: `2px`,
      }}
    >{[
      <div
        style={{
          fontFamily: `Roboto`,
          fontSize: `13px`,
          color: `#FFFFFF`,
          lineHeight: `25px`,
        }}
        onClick={action}
      >
        {bottomBarWidgetText}
      </div>,
      // <div
      //   style={{
      //     display: `flex`,
      //     justifyContent: `center`,
      //     alignItems: `center`,
      //     fontFamily: `Roboto`,
      //     fontWeight: 400,
      //     fontSize: `12px`,
      //     color: `#ffffff`,
      //   }}
      // >
      //   {bytesCountToTotalBytesCountWidgetText}
      // </div>,
    ]}</div>
  </div>;
}
