import React from 'react';

import { logoIcon } from './assets';

export const LoadingWidget = (
  {
  }: {
  },
) => {

  return <>
    <div
      style={{
        width: `388px`,
        height: `100%`,

        display: `flex`,
        justifyContent: `center`,
        alignItems: `center`,
        flexDirection: `column`,
      }}>
      <img
        style={{
          width: `245px`,
          height: `auto`,
        }}
        src={logoIcon}
      />
    </div>
    <div
      style={{
        boxSizing: `border-box`,
        position: `absolute`,
        width: `230px`,
        height: `32px`,
        bottom: 0,
        background: `linear-gradient(95.2deg, rgba(255, 0, 0, 0.36) 4.17%, rgba(255, 0, 0, 0.6) 95.83%)`,
        display: `flex`,
        alignItems: `center`,
        paddingLeft: `10px`,
      }}
    >
      <span
        style={{
          fontFamily: `Roboto`,
          fontSize: `13px`,
          lineHeight: `25px`,
          color: `#FFFFFF`,
        }}
      >
        LOADING...
      </span>
    </div>
  </>;
};
