import React from 'react';

import { assert } from './assert';
import { useDefinedState } from './use_defined_state';
import { LabelButtonWidget } from './label_button_widget';

export const PerformAppActionWidget = (
  {
    appId,
    appBranchId,
    performAction,
    performActionButtonWidget,
    checkForUpdatesButtonWidget,
  }: {
    appId: string;
    appBranchId: string;
    performAction: () => void;
    performActionButtonWidget: {
      labelWidget: {
        text: string;
        htmlStyle: {
          color: string;
          backgroundColor?: string;
          background?: string;
        };
        hoveredHtmlStyle: {
          color: string;
          backgroundColor?: string;
          background?: string;
        };
      };
    };
    checkForUpdatesButtonWidget: {
      isVisible: boolean;
    };
  },
) => {

  const state = useDefinedState();

  const appState = state.apps[appId];

  assert(appState !== undefined);

  const appBranchState = appState.branches[appBranchId];

  assert(appBranchState !== undefined);

  return <div
    style={{
      height: `100%`,
      marginRight: `10px`,

      display: `flex`,
      justifyContent: `flex-end`,
      alignItems: `center`,
    }}
  >
    {[
      checkForUpdatesButtonWidget.isVisible && appBranchState.installedVersionId
        ? <div style={{
          fontFamily: `Roboto`,
          fontSize: `13px`,
          lineHeight: `25px`,
          color: `#ffffff`,
          marginLeft: `10px`,
          position: `absolute`,
          left: 0,
        }}>
          V{appBranchState.installedVersionId}
        </div>
        : undefined,
      <LabelButtonWidget
        htmlStyle={{
          width: `100px`,
          height: `22px`,

          cursor: `pointer`,
        }}
        labelWidget={{
          getHtmlStyle: ({ isHovered }) => ({
            width: `100%`,
            height: `100%`,

            fontFamily: `Roboto`,
            fontSize: `13px`,

            ...(
              isHovered
                ? performActionButtonWidget.labelWidget.hoveredHtmlStyle
                : performActionButtonWidget.labelWidget.htmlStyle
            ),

            display: `flex`,
            justifyContent: `center`,
            alignItems: `center`,
          }),
          text: performActionButtonWidget.labelWidget.text,
        }}
        onClicked={performAction}
      />,
    ]}
  </div>;
};
